  import React, { useState, useEffect, useRef } from 'react';
  import { SectionsContainer, Section, ScrollToTopOnMount } from 'react-fullpage';
  import swal from 'sweetalert';
  import { doc, setDoc, getDoc, collection } from 'firebase/firestore';
  import { isAndroid } from 'react-device-detect';
  import { getStorage, ref, uploadString, getDownloadURL } from 'firebase/storage';
  import { Input, Button,  } from 'antd';
  import { auth, firestore } from '../firebase';
  import { useNavigate } from 'react-router-dom';


const data = [
{
title: 'Welcome to the Survey!',
id: 'introduction',
link: 'first_name',

i: 0
},
{
title: 'lets start with your first name? *',
id: 'first_name',
link: 'last_name',
i: 1
},
{
title: 'and your last name? *',
id: 'last_name',
link: 'email',
i: 2
},
{
title: 'Email *',
subtext: 'Please use the email you signed up with',
id: 'email',
link: 'occupation',
i: 3
},
{
title: 'Expertise? *',
subtext: 'Please list all your areas of expertise',
id: 'occupation',
link: 'evidence',
i: 4
},
{
title: 'Certifications? *',
subtext: 'Please provide the name of certificates to support your expertise',
id: 'evidence',
link: 'cv_upload',
i: 5
},
{
title: 'Upload Certificate, Cv etc. (PDF or Word Document) ',
subtext: 'Please provide relevant documentation to support your expertise',
id: 'cv_upload',
link: 'websites',
type: 'file',
i: 6
},
{
title: 'Personal Website/Linkedin? ',
subtext: 'Optional: If you think it would support your application, please provide relevant links',
id: 'websites',
link: 'final',
i: 7
},
{
title: 'Additional Information? ',
subtext: 'Feel free to add any further information or thoughts that you believe will support your application',
id: 'final',
link: '',
type: 'textarea',
i: 8
}
];

const anchorFunc = (anchor_data) => {
return anchor_data.map((item) => item.id);
};

const FullPage = (props) => {
const [allFieldsFilled, setAllFieldsFilled] = useState(false);
const [submitButtonEnabled, setSubmitButtonEnabled] = useState(false);
const [emailValid, setEmailValid] = useState(true);
const [cvFile, setCvFile] = useState([]);
const [cvDownloadURL, setCvDownloadURL] = useState('');
const [obj, setObj] = useState({});
const [loading, setLoading] = useState(false);
const inputRef = useRef(null);
const storageRef = getStorage();
const [showAndroidMessage, setShowAndroidMessage] = useState(false);
const [showIntroduction, setShowIntroduction] = useState(true);
const [inputCharCount, setInputCharCount] = useState({});
const [isApproved, setIsApproved] = useState(false);

const isMobile = () => {
  return (
    typeof window !== 'undefined' &&
    window.innerWidth <= 768 // Adjust this width threshold as needed for your definition of "mobile"
  );
};



const [textareaCharCount, setTextareaCharCount] = useState(0); 

const navigate = useNavigate()

const [showPendingPopup, setShowPendingPopup] = useState(false);

const checkApplicationStatus = async () => {
  const user = auth.currentUser;
  if (!user) {
    return;
  }
  const currentUserUID = user.uid;

  const docRef = doc(firestore, 'expertApplications', currentUserUID);
  try {
    const docSnap = await getDoc(docRef);
    if (docSnap.exists()) {
      const data = docSnap.data();
      const approvalStatus = data.approved;
      setIsApproved(approvalStatus); // Set isApproved based on retrieved data
      if (!approvalStatus) {
        setShowPendingPopup(true);
      }
    }
  } catch (error) {
    // Handle error
  }
};


useEffect(() => {
checkApplicationStatus();
}, []); 


useEffect(() => {

if (!showIntroduction) {
const firstInput = document.getElementById('1');
if (firstInput) {
firstInput.focus();
}
}
}, [showIntroduction]);

useEffect(() => {
if (isAndroid) {
setShowAndroidMessage(true);

setTimeout(() => {
setShowAndroidMessage(false);
}, 5000);
}
}, []);


const inputHandler = (e) => {
setObj({
...obj,
[e.target.name]: e.target.value,
});
if (e.target.name === 'email') {
const emailRegex = /^\S+@\S+\.\S+$/;
setEmailValid(emailRegex.test(e.target.value));
}
checkAllFieldsFilled(data);
if (e.target.name === 'final') {
const charCount = e.target.value.length;
setInputCharCount(charCount);
setTextareaCharCount(charCount); // Update the character count for the textarea
}
};
const handleFileUpload = (e) => {
const selectedFiles = e.target.files;
setCvFile([...cvFile, ...selectedFiles]);
};

const removeFile = (index) => {
const updatedFiles = [...cvFile];
updatedFiles.splice(index, 1);
setCvFile(updatedFiles);
};

const checkAllFieldsFilled = (data) => {
const requiredFields = ['first_name', 'last_name', 'email', 'occupation', 'evidence'];
const allFilled = requiredFields.every((field) => obj[field] !== undefined && obj[field] !== '');

setAllFieldsFilled(allFilled);
setSubmitButtonEnabled(allFilled || (cvFile.length > 0));
};

useEffect(() => {
checkAllFieldsFilled(data);
}, [obj, cvFile]);

const submitHandler = async () => {
checkAllFieldsFilled();
if (showPendingPopup) {
// If the pending application popup is shown, do nothing
return;
}

if (allFieldsFilled && emailValid) {
if (cvFile) {
setLoading(true);

const uploadFiles = async () => {
try {
const storageRef = getStorage();
const cvRefs = cvFile.map((file) => ref(storageRef, `user_uploads/${file.name}`));
const dataURLs = [];

for (const file of cvFile) {
const dataURL = await new Promise((resolve, reject) => {
const reader = new FileReader();
reader.onload = (e) => {
resolve(e.target.result);
};
reader.onerror = (error) => {
reject(error);
};
reader.readAsDataURL(file);
});

dataURLs.push(dataURL);
}

const uploadTasks = dataURLs.map((dataURL, index) => {
return uploadString(cvRefs[index], dataURL, 'data_url');
});

await Promise.all(uploadTasks);

const downloadURLs = await Promise.all(cvRefs.map((cvRef) => getDownloadURL(cvRef)));
setCvDownloadURL(downloadURLs);

const user = auth.currentUser;
if (user) {
const applicationData = {
...obj,
cv_download_urls: downloadURLs,
approved: false,
};

const createApplication = async () => {
  try {
    const userProfilesCollection = collection(firestore, 'userProfiles');
    const userProfileDoc = doc(userProfilesCollection, user.uid);
    const userProfileData = (await getDoc(userProfileDoc)).data();
    if (userProfileData) {
      const currentPoints = userProfileData.points;
      const customURL = userProfileData.customURL;
      const address = userProfileData.address;
      const city = userProfileData.city;
      const county = userProfileData.county;
      const mobileNumber = userProfileData.mobileNumber;
      const postcode = userProfileData.postcode;
      const photoURL = userProfileData.photoURL;

      const applicationData = {
        ...obj,
        cv_download_urls: downloadURLs,
        approved: false,
        points: currentPoints + 50,
        customURL: customURL,
        address: userProfileData.address || null, // Add address conditionally
        city: userProfileData.city || null, // Add city conditionally
        county: userProfileData.county || null, // Add county conditionally
        mobileNumber: userProfileData.mobileNumber || null, // Add mobileNumber conditionally
        postcode: userProfileData.postcode || null, // Add postcode conditionally
        photoURL: userProfileData.photoURL || null,
      };

      const docRef = await setDoc(doc(firestore, 'expertApplications', user.uid), applicationData);

      // Now, update the points in userProfiles with the same currentPoints (no change in points)
      await setDoc(userProfileDoc, { points: currentPoints }, { merge: true });

      swal({
        title: '',
        text: 'Thanks for completing the survey. We will reach out as soon as we have made a decision!',
        icon: 'success',
        dangerMode: false,
      }).then(() => {
        // Use navigate for navigation in Gatsby
        navigate('/');
      });
    }
  } catch (error) {
  }
};

createApplication();
}
} catch (error) {
} finally {
setLoading(false);
}
};

await uploadFiles();
} else {
const user = auth.currentUser;
if (user) {
const applicationData = {
...obj,
cv_download_urls: [],
approved: false,
};

const createApplication = async () => {
try {
const docRef = await setDoc(doc(firestore, 'expertApplications', user.uid), applicationData);
swal({
title: '',
text: 'Thanks for completing the survey. We will reach out as soon as we have made a decision!',
icon: 'success',
dangerMode: false,
}).then(() => {
// Use navigate for navigation in Gatsby
navigate('/');
});
} catch (error) {
}
};

createApplication();
}
}
} else {
if (!allFieldsFilled) {
swal({
title: 'Error',
text: 'Please fill out all the required fields before submitting.',
icon: 'error',
dangerMode: true,
});
}
if (!emailValid) {
swal({
title: 'Error',
text: 'Please provide a valid email address.',
icon: 'error',
dangerMode: true,
});
}
}
};
const focusOnNextInput = (currentIndex) => { const isMobileDevice = /Mobi|Android/i.test(navigator.userAgent);   if (isMobileDevice) { return; } if (currentIndex < data.length - 1) { const newIndex = currentIndex + 1; const newAnchor = data[newIndex].id; location.href = `#${newAnchor}`; const targetElement = document.getElementById((newIndex + 1).toString()); if (targetElement) { setTimeout(() => { targetElement.focus(); }, 1000); } } };
  
  const handleNavigation = (action, currentIndex) => {
    const isMobileDevice = /Mobi|Android/i.test(navigator.userAgent);
    
    if (isMobileDevice && action === 'next') {
      return; // Exit the function if trying to navigate forward on a mobile device
    }
  
    let newIndex;
  
    if (action === 'next' && currentIndex < data.length - 1) {
      newIndex = currentIndex + 1;
    } else if (action === 'previous' && currentIndex > 0) {
      newIndex = currentIndex - 1;
    } else {
      // Do nothing if trying to navigate past the first or last section
      return;
    }
  
    const newAnchor = data[newIndex].id;
    location.href = `#${newAnchor}`;
  
    const targetElement = document.getElementById((newIndex + 1).toString());
    if (targetElement) {
      setTimeout(() => {
        targetElement.focus();
      }, 1000);
    }
  };
  
  const handleKeyPress = (e, currentIndex) => {
    const isMobileDevice = /Mobi|Android/i.test(navigator.userAgent);
    
    if (isMobileDevice && e.key === 'Enter') {
      e.preventDefault(); // Prevent the default behavior of the Enter key on mobile
    }
  
    if (!isMobileDevice && e.key === 'Enter') {
      focusOnNextInput(currentIndex);
    }
  };
  
  
  
 
return (
<div>
<ScrollToTopOnMount />
{showAndroidMessage && (
<div className="android-message">
Use the "OK" button for a smoother experience and to prevent screen shifting.
</div>
)}
{/* Add the pending application popup here */}
{showPendingPopup && (
<div className="blurred-background">
<div className="pending-application-popup">
<p>Your application is pending. Please wait for approval.</p>
<a href="/" className="dropdown-item" style={{ marginRight: 0, marginLeft: 0}}>Return Home</a>

</div>
</div>
)}
{!showPendingPopup && isApproved && (
  <div className="blurred-background">
<div className="pending-application-popup">
      <p>Congratulations! You are already an approved expert. Macio</p>
      <p>If you wish to add another expertise, please send an email to tio@expertapplication.com</p>
      <a href="/" className="dropdown-item" style={{ marginRight: 0, marginLeft: 0}}>Return Home</a>
    </div>
  </div>
)}


<SectionsContainer
  sectionClassName="section"
  anchors={anchorFunc(data)}
  scrollBar={false}
  navigation={true}
  verticalAlign={false}
  sectionPaddingTop="50px"
  sectionPaddingBottom="50px"
  arrowNavigation={false}
>
  {data.map((item, i) => (
    <Section key={i}>
      {i === 0 ? (
        // Render the introduction section for i: 0
        <div className="introduction-page">
          <div className="row">
            <div className="formintroduction col-md-6">
              <h1>Ready to become an Expert?</h1>
              <div>
                <span className="introductiontext">
                  Welcome to our Expert Network, a distinguished community where verified experts in various fields provide valuable insights to users seeking reliable answers. Our experts have undergone a rigorous vetting process, ensuring top-tier expertise.
                  <br></br>
                  <br></br>
                  If you're passionate about sharing your knowledge and helping our users, please complete the application form below. We'll review your qualifications, and if you meet our criteria, you'll have the opportunity to join our platform and assist those seeking expert guidance.
                </span>
              </div>
              <div>
              <Button className="formintroductionbtn mt-3 mb-3" id="start-survey-btn" onClick={() => handleNavigation('next', 0)}>
  Start Survey
</Button>
                <div className="introductiontime" style={{ display: 'flex', alignItems: 'center' }}>
                  <svg xmlns="http://www.w3.org/2000/svg" color="#212529" fill="currentColor" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25" style={{ marginRight: '3px' }}>
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm.75-13a.75.75 0 00-1.5 0v5c0 .414.336.75.75.75h4a.75.75 0 000-1.5h-3.25V5z" clipRule="evenodd" />
                  </svg>
                  Takes 5 minutes
                </div>
              </div>
            </div>
<div className="col-md-6 d-flex justify-content-center align-items-center">     
       <img src={`${process.env.PUBLIC_URL}/images/Tios/Learning-Tio.webp`} alt="TIO illustration" className="img-fluid-Typeform" />
            </div>
          </div>
        </div>
      ) : (
        // Render other sections for i > 0
        <header className="App-header">
        <div className="questionContainer">
          <div className="title">
            <h2>
              <span className="count">{i} &nbsp;</span>&nbsp;
              <span className="page1-block h3">{item.title}</span>
            </h2>
            {item.subtext && (
              <p className="subtext">{item.subtext}</p>
            )}
          </div>
{item.type === 'file' ? (
<div>
<div className="uploadcontainer">
<input
className="uploadfield"
type="file"
accept=".pdf, .doc, .docx"
onChange={handleFileUpload}
multiple
/>
<div className="documentupload">
<svg xmlns="http://www.w3.org/2000/svg" color="#7EA0D6" fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="75" width="75">
<path strokeLinecap="round" strokeLinejoin="round" d="M19.5 14.25v-2.625a3.375 3.375 0 00-3.375-3.375h-1.5A1.125 1.125 0 0113.5 7.125v-1.5a3.375 3.375 0 00-3.375-3.375H8.25m6.75 12l-3-3m0 0l-3 3m3-3v6m-1.5-15H5.625c-.621 0-1.125.504-1.125 1.125v17.25c0 .621.504 1.125 1.125 1.125h12.75c.621 0 1.125-.504 1.125-1.125V11.25a9 9 0 00-9-9z" />
</svg>
<div>
<div className="choosefile mt-2">
Choose file
</div>
{' '}
or{' '}
<div className="draghere">
drag here
</div>
</div>
</div>
</div>
{cvFile.map((file, index) => (
<div key={index}>
<p>Uploaded File {index + 1}: {file.name} <span onClick={() => removeFile(index)} style={{ marginLeft: '5px', cursor: 'pointer' }}>‚ùå</span></p>
</div>
))}
</div>
) : item.type === 'textarea' ? (
<div>
<Input.TextArea
  placeholder="Type your answer here..."
  name={item.id}
  id={i + 1}
  className="inputtypearea"
  onChange={inputHandler}
  autoSize={{ minRows: 3, maxRows: 6 }}
/>
<div className="char-count" style={{ color: textareaCharCount > 200 ? 'red' : 'inherit' }}>
  {textareaCharCount}/200 characters
</div>
</div>
) : (
<Input
  placeholder="Type your answer here..."
  name={item.id}
  id={i + 1}
  className="inputtypeform"
  onKeyDown={(e) => handleKeyPress(e, item.i)}
  onChange={inputHandler}
/>
)}
<br />
<div id="submit-box">
{i === data.length - 1 ? (
<Button
id="submit-btn"
onClick={submitHandler}
disabled={textareaCharCount > 200} 
className={textareaCharCount > 200 ? 'disabled-button' : ''}
loading={loading}
style={{ width: '100px' }}
>
{loading ? '' : 'SUBMIT'}
</Button>
) : (
<div style={{ display: 'flex', alignItems: 'center' }}>
<Button id="enter-btn" onClick={() => handleNavigation('next', item.i)}>
  OK
</Button>
{!isMobile && (
<span className="press-enter">
press <span className="bold">ENTER</span>
</span>
)}

<div className="next-previous-buttons">
<Button className="next-btn" onClick={() => handleNavigation('next', item.i)} style={{ marginRight: '10px' }}>
      <svg xmlns="http://www.w3.org/2000/svg" color="#ffffff" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" height="100%" width="100%">
  <path strokeLinecap="round" strokeLinejoin="round" d="M19.5 8.25l-7.5 7.5-7.5-7.5" />
</svg>
  </Button>
  <Button className="next-btn" onClick={() => handleNavigation('previous', item.i)}>
  <svg xmlns="http://www.w3.org/2000/svg" color="#ffffff" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" height="100%" width="100%">
    <path strokeLinecap="round" strokeLinejoin="round" d="M4.5 15.75l7.5-7.5 7.5 7.5" />
  </svg>
</Button>
</div>
</div>


)}
</div>

</div>





        </header>
      )}
    </Section>
  ))}
</SectionsContainer>
</div>
);
};

export default React.memo(FullPage);
